html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif !important;
}

p {
  color: #000000;
}

a {
  text-decoration: none;
  margin: 0;
}

img,
picture {
  width: 100%;
  display: block;
}

.subtitulo {
  font-size: 0.8em;
}

.titulo {
  padding: 0.2rem;
  border-radius: 0.1rem;
  background-color: #FFA500;
  color: #FFFFFF;
  font-weight: bold;
}

.navbar {
  background-color: #FFA500;
}

.active {
  font-weight: bold !important;
  color: #FFFFFF !important;
}

.contenedorBarraNavegacion {
  display: flex;
  justify-content: center !important;
}
.contenedorBarraNavegacion .navbar-collapse {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .contenedorBarraNavegacion .navbar-collapse {
    margin-top: 0rem;
    flex-direction: row;
    justify-content: space-between;
  }
}

.navbar-brand {
  margin: 0rem !important;
}
.navbar-brand picture {
  height: 3rem;
  overflow: hidden;
  border-radius: 50%;
}
.navbar-brand picture img {
  height: 100%;
  scale: 1.5;
  transition: scale 0.2s ease-in-out;
}
.navbar-brand picture:hover img {
  scale: 1.8;
  transition: scale 0.2s ease-in-out;
}

.contenedorFormulario {
  padding: 4rem 6rem;
}
.contenedorFormulario form {
  padding: 0 2rem;
}
@media (min-width: 768px) {
  .contenedorFormulario form {
    padding: 0;
  }
}
.contenedorFormulario form h1 {
  color: #FFFFFF;
  font-weight: bold;
  padding: 1rem;
}
.contenedorFormulario .contLbl {
  display: flex;
  flex-direction: column;
}
.contenedorFormulario .lbl {
  text-align: center;
  font-size: 1.2rem;
  color: #000080;
  font-weight: bold;
  cursor: pointer;
}
@media (min-width: 768px) {
  .contenedorFormulario .lbl {
    float: right;
    text-align: right;
  }
}

@media (min-width: 768px) {
  .card-text {
    height: 12rem;
  }
}

.btnFluido {
  width: 100%;
}

.card-img-top {
  display: flex;
  justify-content: center; /* Centra horizontalmente */
  align-items: center; /* Centra verticalmente */
  overflow: hidden;
  height: 28rem;
}
.card-img-top:hover img {
  scale: 0.8;
  transition: scale 0.2s ease;
}
.card-img-top img {
  transition: scale 0.2s ease-in-out;
  width: 100%;
  object-fit: cover;
}

.titulo__esp {
  padding: 1rem;
}

.seccionMasInformacion {
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
  max-height: auto;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .seccionMasInformacion {
    height: 45rem;
    max-height: 95vh;
  }
}
.seccionMasInformacion .row {
  height: 100%;
}
.seccionMasInformacion .row .contenedorImgInformativa {
  height: 100%;
  max-height: 25rem;
  display: flex;
  justify-content: center;
  position: relative;
  padding: 0;
  overflow: hidden;
}
@media (min-width: 768px) {
  .seccionMasInformacion .row .contenedorImgInformativa {
    max-height: 100%;
  }
}
.seccionMasInformacion .row .contenedorImgInformativa picture {
  height: 100%;
  width: 100%;
}
.seccionMasInformacion .row .contenedorImgInformativa picture img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}
.seccionMasInformacion .row .textosImgInformativa {
  text-align: center;
  padding-top: 1rem;
  top: 0;
  position: absolute;
  color: #FFFFFF;
}
.seccionMasInformacion .row .textosImgInformativa h2 {
  font-family: "Agbalumo", "Open Sans", sans-serif;
  font-size: 4rem;
  margin-bottom: 0.5rem;
}
.seccionMasInformacion .row .textosImgInformativa p {
  font-weight: bold;
  font-size: 0.8em;
  color: #FFFFFF;
  text-shadow: 0px 0px 0px rgba(0, 0, 0, 0);
  padding: 0.2rem;
  display: inline;
  border-radius: 0.1rem;
  background-color: #FFA500;
}
.seccionMasInformacion .contenedorTarjetasInformativas .row .contenedorFC:hover .tarjetaInformativa {
  transform: rotateY(180deg);
  transition: transform 0.6s;
}
.seccionMasInformacion .contenedorTarjetasInformativas .row .tarjetaInformativa {
  position: relative;
  display: grid;
  place-items: center;
  color: #FFFFFF;
  font-weight: bold;
  min-height: 12rem;
  height: 100%;
  width: 100%;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  padding: 1rem;
}
.seccionMasInformacion .contenedorTarjetasInformativas .row .tarjetaInformativa .tarjetaInformativa-front {
  font-size: 1.5rem;
}
@media (min-width: 1024px) {
  .seccionMasInformacion .contenedorTarjetasInformativas .row .tarjetaInformativa .tarjetaInformativa-front {
    font-size: 2rem;
  }
}
.seccionMasInformacion .contenedorTarjetasInformativas .row .tarjetaInformativa .tarjetaInformativa-back {
  transform: rotateY(180deg);
  display: flex;
  flex-direction: column;
  text-align: center;
}
.seccionMasInformacion .contenedorTarjetasInformativas .row .tarjetaInformativa .tarjetaInformativa-back i {
  font-size: 2.5rem;
}
@media (min-width: 1024px) {
  .seccionMasInformacion .contenedorTarjetasInformativas .row .tarjetaInformativa .tarjetaInformativa-back i {
    font-size: 4rem;
  }
}
.seccionMasInformacion .contenedorTarjetasInformativas .row .tarjetaInformativa .tarjetaInformativa-front, .seccionMasInformacion .contenedorTarjetasInformativas .row .tarjetaInformativa .tarjetaInformativa-back {
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  position: absolute;
}

.bgCol1 {
  background-color: #00C4CC;
}

.bgCol2 {
  background-color: #FF738E;
}

.bgCol3 {
  background-color: #A548FF;
}

.bgCol4 {
  background-color: #FFA53B;
}

.bgCol5 {
  background-color: #6422B8;
}

.bgCol6 {
  background-color: #FFD93B;
}